import React, { useState, useCallback, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import MenuBar from '../MenuBar/MenuBar';
import LeftPanel from '../LeftPanel/LeftPanel';
import CentralPanel from '../CentralPanel/CentralPanel';
import RightPanel from '../RightPanel/RightPanel';
import ParameterModal from '../ParameterModal/ParameterModal';
import ComponentResultsModal from '../ComponentResultsModal/ComponentResultsModal';
import CIResultsModal from '../CIResultsModal/CIResultsModal';
import SaveModal from '../SaveModal/SaveModal';
import LLMConfigModal from '../LLMConfigModal/LLMConfigModal';
import MatlabExportModal from '../MatlabExportModal/MatlabExportModal';
import SHyFTAModal from '../SHyFTAModal/SHyFTAModal';
import ConfirmationModal from '../ConfirmationModal/ConfirmationModal';
import { FaultTreeModel, BaseEvent, Gate, GateType } from '../../types/FaultTree';
import { FaultTreeModification } from '../../types/ChatIntegration';
import { FileService } from '../../services/file-service';
import { useLLMConfig } from '../../contexts/LLMContext';
import { useModelPersistence } from '../../contexts/ModelPersistenceContext';
import { useTheme } from '../../contexts/ThemeContext';
import { usePanel } from '../../contexts/PanelContext';
import { useDeviceType } from '../../hooks/useDeviceType';
import './FaultTreeEditor.css';

const FaultTreeEditor: React.FC = () => {
  const navigate = useNavigate();
  const { showLLMConfigModal, setShowLLMConfigModal, updateLLMConfig } = useLLMConfig();
  const {
    saveFaultTreeSnapshot,
    getFaultTreeSnapshot,
    clearSnapshots,
    saveFaultTreeOpenedFile,
    getFaultTreeOpenedFile
  } = useModelPersistence();
  const { isDarkMode, toggleDarkMode } = useTheme();
  const { isRightPanelCollapsed, toggleRightPanel } = usePanel();
  const [faultTreeModel, setFaultTreeModel] = useState<FaultTreeModel>({
    events: [],
    gates: [],
    connections: []
  });

  const [selectedElement, setSelectedElement] = useState<BaseEvent | Gate | null>(null);
  const [showParameterModal, setShowParameterModal] = useState(false);
  const [showSaveModal, setShowSaveModal] = useState(false);
  const [showMatlabExportModal, setShowMatlabExportModal] = useState(false);
  const [showSHyFTAModal, setShowSHyFTAModal] = useState(false);
  const [showNewModelConfirmation, setShowNewModelConfirmation] = useState(false);
  const [showComponentResultsModal, setShowComponentResultsModal] = useState(false);
  const [showCIResultsModal, setShowCIResultsModal] = useState(false);
  const [selectedResultsElementId, setSelectedResultsElementId] = useState<string | null>(null);
  const [missionTime, setMissionTime] = useState(500); // Default mission time in hours
  const [simulationResultsVersion, setSimulationResultsVersion] = useState(0);
  
  // Ottieni il file aperto dal context
  const openedFile = getFaultTreeOpenedFile();
  
  // Rilevamento automatico del dispositivo
  const deviceType = useDeviceType();

  // Funzione helper per aggiornare i contatori basandosi sui nomi esistenti
  const updateCountersFromModel = useCallback((model: FaultTreeModel) => {
    // Trova il numero pi alto negli eventi base (pattern BE_X)
    let maxEventNumber = 0;
    model.events.forEach(event => {
      const match = event.name.match(/^BE_(\d+)$/);
      if (match) {
        maxEventNumber = Math.max(maxEventNumber, parseInt(match[1]));
      }
    });

    // Trova il numero pi alto nelle porte (pattern GATETYPE_X)
    let maxGateNumber = 0;
    model.gates.forEach(gate => {
      const match = gate.name.match(/_(\d+)$/);
      if (match) {
        maxGateNumber = Math.max(maxGateNumber, parseInt(match[1]));
      }
    });

    // Imposta i contatori al prossimo numero disponibile
    setNextEventNumber(maxEventNumber + 1);
    setNextGateNumber(maxGateNumber + 1);
  }, []);
  
  // Stato per modalit click-to-place
  const [clickToPlaceMode, setClickToPlaceMode] = useState(false);
  const [componentToPlace, setComponentToPlace] = useState<{
    type: 'event' | 'gate';
    gateType?: GateType;
  } | null>(null);

  // Stato per pannello sinistro collassato
  const [isLeftPanelCollapsed, setIsLeftPanelCollapsed] = useState(false);


  // Contatori globali per nomi univoci (incrementali, non decrementano mai)
  const [nextEventNumber, setNextEventNumber] = useState(1);
  const [nextGateNumber, setNextGateNumber] = useState(1);

  // Ref per ottenere le posizioni correnti dal CentralPanel
  const getCurrentPositionsRef = useRef<(() => FaultTreeModel) | null>(null);

  // Auto-collapse pannello sinistro su dispositivi mobili/tablet
  // Il pannello destro mantiene lo stato condiviso del contesto
  useEffect(() => {
    if (deviceType === 'mobile' || deviceType === 'tablet') {
      setIsLeftPanelCollapsed(true);
    } else {
      // Su desktop, espandi il pannello sinistro
      setIsLeftPanelCollapsed(false);
    }
  }, [deviceType]);

  // Ripristina il modello dal snapshot al mount
  useEffect(() => {
    const snapshot = getFaultTreeSnapshot();
    if (snapshot) {
      setFaultTreeModel(snapshot);
      updateCountersFromModel(snapshot);
    }
  }, [getFaultTreeSnapshot, updateCountersFromModel]);

  // Salva automaticamente il snapshot ogni volta che il modello cambia
  useEffect(() => {
    // Solo se il modello non  vuoto, salva lo snapshot
    if (faultTreeModel.events.length > 0 || faultTreeModel.gates.length > 0) {
      saveFaultTreeSnapshot(faultTreeModel);
    }
  }, [faultTreeModel, saveFaultTreeSnapshot]);

  // Listener per aggiornamenti dei risultati simulazione
  useEffect(() => {
    const handleSimulationResultsLoaded = () => {
      console.log(' Simulation results loaded, updating UI');
      setSimulationResultsVersion(prev => prev + 1);
    };

    window.addEventListener('simulationResultsLoaded', handleSimulationResultsLoaded);
    return () => window.removeEventListener('simulationResultsLoaded', handleSimulationResultsLoaded);
  }, []);

  // Gestione aggiunta eventi base
  const handleAddBaseEvent = useCallback(() => {
    if (clickToPlaceMode) {
      // Modalit click-to-place: prepara il componente per il posizionamento
      setComponentToPlace({ type: 'event' });
    } else {
      // Modalit normale: aggiungi subito con posizione casuale
      const newEvent: BaseEvent = {
        id: `event-${Date.now()}`,
        type: 'basic-event',
        name: `BE_${nextEventNumber}`,
        position: { x: 300 + Math.random() * 200, y: 200 + Math.random() * 200 },
        parameters: {},
        failureProbabilityDistribution: { type: 'exponential', lambda: 0.01 }
      };

      setFaultTreeModel(prev => ({
        ...prev,
        events: [...prev.events, newEvent]
      }));
      setNextEventNumber(prev => prev + 1);
    }
  }, [clickToPlaceMode, nextEventNumber]);

  // Gestione aggiunta porte
  const handleAddGate = useCallback((gateType: GateType) => {
    if (clickToPlaceMode) {
      // Modalit click-to-place: prepara il componente per il posizionamento
      setComponentToPlace({ type: 'gate', gateType });
    } else {
      // Modalit normale: aggiungi subito con posizione casuale
      const newGate: Gate = {
        id: `gate-${Date.now()}`,
        type: 'gate',
        gateType,
        name: `${gateType}_${nextGateNumber}`,
        position: { x: 300 + Math.random() * 200, y: 200 + Math.random() * 200 },
        inputs: [],
        isFailureGate: false,
        parameters: {}
      };

      setFaultTreeModel(prev => ({
        ...prev,
        gates: [...prev.gates, newGate]
      }));
      setNextGateNumber(prev => prev + 1);
    }
  }, [clickToPlaceMode, nextGateNumber]);

  // Gestione click su elemento per aprire parametri
  const handleElementClick = useCallback((element: BaseEvent | Gate) => {
    setSelectedElement(element);
    setShowParameterModal(true);
  }, []);

  // Gestione click sul pannello per posizionare componente
  const handlePanelClick = useCallback((position: { x: number; y: number }) => {
    if (!componentToPlace) return;

    if (componentToPlace.type === 'event') {
      const newEvent: BaseEvent = {
        id: `event-${Date.now()}`,
        type: 'basic-event',
        name: `BE_${nextEventNumber}`,
        position,
        parameters: {},
        failureProbabilityDistribution: { type: 'exponential', lambda: 0.01 }
      };

      setFaultTreeModel(prev => ({
        ...prev,
        events: [...prev.events, newEvent]
      }));
      setNextEventNumber(prev => prev + 1);
    } else if (componentToPlace.type === 'gate' && componentToPlace.gateType) {
      const newGate: Gate = {
        id: `gate-${Date.now()}`,
        type: 'gate',
        gateType: componentToPlace.gateType,
        name: `${componentToPlace.gateType}_${nextGateNumber}`,
        position,
        inputs: [],
        isFailureGate: false,
        parameters: {}
      };

      setFaultTreeModel(prev => ({
        ...prev,
        gates: [...prev.gates, newGate]
      }));
      setNextGateNumber(prev => prev + 1);
    }

    // NON resettare componentToPlace - mantieni la selezione per posizionamenti multipli
    // setComponentToPlace(null); // Rimosso
  }, [componentToPlace, nextEventNumber, nextGateNumber]);

  // Toggle modalit click-to-place
  const handleToggleClickToPlace = useCallback(() => {
    setClickToPlaceMode(prev => !prev);
    setComponentToPlace(null); // Reset componente selezionato
  }, []);

  // Gestione aggiornamento modello con preservazione posizioni correnti
  const handleModelChange = useCallback((newModel: FaultTreeModel) => {
    // Prima ottieni le posizioni correnti dal CentralPanel se disponibile
    if (getCurrentPositionsRef.current) {
      const modelWithCurrentPositions = getCurrentPositionsRef.current();

      // Merge il nuovo modello con le posizioni correnti
      const updatedModel = {
        ...newModel,
        events: newModel.events.map(event => {
          const currentEvent = modelWithCurrentPositions.events.find(e => e.id === event.id);
          return currentEvent ? { ...event, position: currentEvent.position } : event;
        }),
        gates: newModel.gates.map(gate => {
          const currentGate = modelWithCurrentPositions.gates.find(g => g.id === gate.id);
          return currentGate ? { ...gate, position: currentGate.position } : gate;
        })
      };

      setFaultTreeModel(updatedModel);
    } else {
      // Fallback al modello originale se non  possibile ottenere le posizioni correnti
      setFaultTreeModel(newModel);
    }
  }, []);

  // Gestione aggiornamento parametri
  const handleUpdateElement = useCallback((updatedElement: BaseEvent | Gate) => {
    if (updatedElement.type === 'basic-event') {
      setFaultTreeModel(prev => ({
        ...prev,
        events: prev.events.map(event => 
          event.id === updatedElement.id ? updatedElement as BaseEvent : event
        )
      }));
    } else {
      setFaultTreeModel(prev => ({
        ...prev,
        gates: prev.gates.map(gate => 
          gate.id === updatedElement.id ? updatedElement as Gate : gate
        )
      }));
    }
    setShowParameterModal(false);
    setSelectedElement(null);
  }, []);

  // Gestione visualizzazione risultati simulazione
  const handleShowResults = useCallback((elementId: string) => {
    setSelectedResultsElementId(elementId);
    setShowComponentResultsModal(true);
    setShowParameterModal(false); // Chiudi il modal dei parametri
  }, []);

  // Gestione visualizzazione risultati CI
  const handleShowCIResults = useCallback(() => {
    setShowCIResultsModal(true);
    setShowParameterModal(false); // Chiudi il modal dei parametri
  }, []);

  // Gestione salvataggio file
  const handleSaveFile = useCallback(async () => {
    try {
      const result = await FileService.saveModelWithOverwrite(
        faultTreeModel,
        'fault-tree',
        openedFile?.fileHandle,
        openedFile?.filename
      );
      saveFaultTreeOpenedFile(result);
      alert('Fault Tree salvato con successo!');
    } catch (error) {
      alert(`Errore durante il salvataggio: ${error instanceof Error ? error.message : 'Errore sconosciuto'}`);
    }
  }, [faultTreeModel, saveFaultTreeOpenedFile, openedFile]);

  // Gestione apertura file
  const handleOpenFile = useCallback(async (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      try {
        const { model } = await FileService.openModelWithValidation(file, 'fault-tree');
        setFaultTreeModel(model as FaultTreeModel);
        updateCountersFromModel(model as FaultTreeModel);
        
        saveFaultTreeOpenedFile({
          url: URL.createObjectURL(file),
          filename: file.name
        });
        
        alert('Fault Tree caricato con successo!');
      } catch (error) {
        alert(`Errore nel caricamento del file: ${error instanceof Error ? error.message : 'Errore sconosciuto'}`);
      }
    }
  }, [updateCountersFromModel, saveFaultTreeOpenedFile]);

  // Gestione apertura file con File System Access (per sovrascrittura)
  const handleOpenFileWithFileSystem = useCallback(async () => {
    if (typeof window !== 'undefined' && 'showOpenFilePicker' in window) {
      try {
        const [fileHandle] = await (window as any).showOpenFilePicker({
          types: [{
            description: 'JSON Files',
            accept: {
              'application/json': ['.json']
            }
          }]
        });
        
        const file = await fileHandle.getFile();
        const { model } = await FileService.openModelWithValidation(file, 'fault-tree');
        setFaultTreeModel(model as FaultTreeModel);
        updateCountersFromModel(model as FaultTreeModel);
        
        saveFaultTreeOpenedFile({
          url: URL.createObjectURL(file),
          filename: file.name,
          fileHandle: fileHandle
        });
        
        alert('Fault Tree caricato con successo!');
      } catch (error) {
        if (error instanceof Error && error.name === 'AbortError') {
          // L'utente ha annullato la selezione
          return;
        }
        if (error instanceof Error && error.name === 'NotAllowedError') {
          // L'utente ha negato i permessi
          alert('Permessi negati per l\'accesso ai file. Usa il pulsante "Apri" per selezionare manualmente un file.');
          return;
        }
        // Se c' un errore diverso dall'annullamento o permessi negati, mostra l'errore ma non aprire il fallback
        console.error('Errore durante l\'apertura del file:', error);
        alert(`Errore durante l'apertura del file: ${error instanceof Error ? error.message : 'Errore sconosciuto'}`);
      }
    } else {
      // Fallback al metodo tradizionale solo se l'API non  supportata
      const fileInput = document.createElement('input');
      fileInput.type = 'file';
      fileInput.accept = '.json,.xml,.csv';
      fileInput.onchange = (e) => handleOpenFile(e as any);
      fileInput.click();
    }
  }, [handleOpenFile, updateCountersFromModel, saveFaultTreeOpenedFile]);

  // Gestione esportazione XML
  const handleExportXML = useCallback(async () => {
    try {
      await FileService.exportToXML(faultTreeModel);
    } catch (error) {
      alert(`Errore durante l'esportazione XML: ${error instanceof Error ? error.message : 'Errore sconosciuto'}`);
    }
  }, [faultTreeModel]);

  // Gestione esportazione CSV
  const handleExportCSV = useCallback(async () => {
    try {
      await FileService.exportToCSV(faultTreeModel);
    } catch (error) {
      alert(`Errore durante l'esportazione CSV: ${error instanceof Error ? error.message : 'Errore sconosciuto'}`);
    }
  }, [faultTreeModel]);

  // Gestione apertura SaveModal
  const handleShowSaveModal = useCallback(() => {
    setShowSaveModal(true);
  }, []);

  // Gestione apertura LLM Config Modal
  const handleShowLLMConfig = useCallback(() => {
    setShowLLMConfigModal(true);
  }, [setShowLLMConfigModal]);

  // Gestione apertura MATLAB Export Modal
  const handleShowMatlabExport = useCallback(() => {
    setShowMatlabExportModal(true);
  }, []);

  // Gestione apertura SHyFTA Modal
  const handleShowSHyFTA = useCallback(() => {
    setShowSHyFTAModal(true);
  }, []);

  // Gestione cambio mission time
  const handleMissionTimeChange = useCallback((value: number) => {
    setMissionTime(value);
  }, []);

  // Gestione salvataggio configurazione LLM
  const handleLLMConfigChange = useCallback((newConfig: any) => {
    updateLLMConfig(newConfig);
  }, [updateLLMConfig]);

  // Gestione toggle dark mode
  const handleToggleDarkMode = useCallback(() => {
    toggleDarkMode();
  }, [toggleDarkMode]);

  // Gestione navigazione tra editor
  const handleNavigateToMarkov = useCallback(() => {
    // Prima ottieni le posizioni correnti dal React Flow e aggiorna il modello
    if (getCurrentPositionsRef.current) {
      const modelWithCurrentPositions = getCurrentPositionsRef.current();
      // Aggiorna il modello con le posizioni correnti
      setFaultTreeModel(modelWithCurrentPositions);

      // Salva lo snapshot e naviga nel prossimo render cycle
      setTimeout(() => {
        saveFaultTreeSnapshot(modelWithCurrentPositions);
        navigate('/markov-chain-editor');
      }, 0);
    } else {
      // Fallback al modello attuale
      saveFaultTreeSnapshot(faultTreeModel);
      navigate('/markov-chain-editor');
    }
  }, [navigate, saveFaultTreeSnapshot, faultTreeModel]);

  const handleNavigateToFaultTree = useCallback(() => {
    // Already on fault tree editor, no need to navigate
  }, []);

  const handleLogout = useCallback(() => {
    navigate('/');
  }, [navigate]);

  // Gestione toggle pannello sinistro
  const handleToggleLeftPanel = useCallback(() => {
    setIsLeftPanelCollapsed(prev => !prev);
  }, []);

  // Gestione toggle pannello destro
  const handleToggleRightPanel = useCallback(() => {
    toggleRightPanel();
  }, [toggleRightPanel]);

  // Gestione esportazione codice
  const handleExportCode = useCallback(async () => {
    try {
      const codeStr = generateFaultTreeCode(faultTreeModel);
      await FileService.exportCode(codeStr);
    } catch (error) {
      alert(`Errore durante l'esportazione del codice: ${error instanceof Error ? error.message : 'Errore sconosciuto'}`);
    }
  }, [faultTreeModel]);

  // Funzione per trovare tutti gli elementi collegati a una gate (ricorsiva)
  const findConnectedElements = useCallback((gateId: string, visited = new Set<string>()): string[] => {
    if (visited.has(gateId)) return [];
    visited.add(gateId);
    
    const gate = faultTreeModel.gates.find(g => g.id === gateId);
    if (!gate) return [];
    
    const connected: string[] = [];
    
    // Aggiungi tutti gli input della gate
    gate.inputs.forEach(inputId => {
      connected.push(inputId);
      
      // Se l'input  una gate, trova ricorsivamente i suoi elementi collegati
      const inputGate = faultTreeModel.gates.find(g => g.id === inputId);
      if (inputGate) {
        connected.push(...findConnectedElements(inputId, visited));
      }
    });
    
    return connected;
  }, [faultTreeModel.gates]);

  // Gestione cancellazione elemento
  const handleDeleteElement = useCallback((elementId: string) => {
    const element = [...faultTreeModel.events, ...faultTreeModel.gates].find(e => e.id === elementId);
    if (!element) return;

    if (element.type === 'gate') {
      // Cancellazione gate: elimina tutti gli elementi collegati
      const connectedIds = findConnectedElements(elementId);
      
      setFaultTreeModel(prev => ({
        ...prev,
        events: prev.events.filter(event => !connectedIds.includes(event.id)),
        gates: prev.gates.filter(gate => gate.id !== elementId && !connectedIds.includes(gate.id)),
        connections: prev.connections.filter(conn => 
          conn.source !== elementId && 
          conn.target !== elementId &&
          !connectedIds.includes(conn.source) &&
          !connectedIds.includes(conn.target)
        )
      }));
    } else {
      // Cancellazione evento base: rimuovi solo l'evento e i suoi collegamenti
      setFaultTreeModel(prev => ({
        ...prev,
        events: prev.events.filter(event => event.id !== elementId),
        connections: prev.connections.filter(conn => 
          conn.source !== elementId && conn.target !== elementId
        ),
        // Rimuovi l'evento dagli input delle gate
        gates: prev.gates.map(gate => ({
          ...gate,
          inputs: gate.inputs.filter(inputId => inputId !== elementId)
        }))
      }));
    }
  }, [faultTreeModel, findConnectedElements]);

  // Gestione cancellazione collegamento
  const handleDeleteConnection = useCallback((connectionId: string) => {
    const connection = faultTreeModel.connections.find(c => c.id === connectionId);
    if (!connection) return;

    setFaultTreeModel(prev => ({
      ...prev,
      connections: prev.connections.filter(conn => conn.id !== connectionId),
      // Rimuovi l'input dalla gate target
      gates: prev.gates.map(gate => 
        gate.id === connection.target 
          ? { ...gate, inputs: gate.inputs.filter(inputId => inputId !== connection.source) }
          : gate
      )
    }));
  }, [faultTreeModel]);

  // Funzione per riorganizzare tutti i componenti al centro
  const handleReorganizeComponents = useCallback(() => {
    const allElements = [...faultTreeModel.events, ...faultTreeModel.gates];
    if (allElements.length === 0) return;

    // Calcola il centro dello schermo (considerando che il pannello centrale  il focus)
    const centerX = 400; // Centro approssimativo del pannello centrale
    const centerY = 300;
    
    // Spaziatura tra componenti
    const spacing = 120;
    
    // Calcola la griglia ottimale
    const cols = Math.ceil(Math.sqrt(allElements.length));
    const rows = Math.ceil(allElements.length / cols);
    
    // Posiziona ogni elemento in una griglia ordinata
    const updatedElements = allElements.map((element, index) => {
      const row = Math.floor(index / cols);
      const col = index % cols;
      
      const newX = centerX + (col - (cols - 1) / 2) * spacing;
      const newY = centerY + (row - (rows - 1) / 2) * spacing;
      
      return {
        ...element,
        position: { x: newX, y: newY }
      };
    });

    // Aggiorna il modello
    setFaultTreeModel(prev => ({
      ...prev,
      events: updatedElements.filter(el => el.type === 'basic-event') as BaseEvent[],
      gates: updatedElements.filter(el => el.type === 'gate') as Gate[]
    }));
  }, [faultTreeModel]);

  // Gestione generazione fault tree dal chatbot
  const handleGenerateFaultTree = useCallback((generatedModel: FaultTreeModel) => {
    // Merge del modello generato con quello esistente
    const mergedModel = {
      events: [...faultTreeModel.events, ...generatedModel.events],
      gates: [...faultTreeModel.gates, ...generatedModel.gates],
      connections: [...faultTreeModel.connections, ...generatedModel.connections],
      topEvent: generatedModel.topEvent || faultTreeModel.topEvent
    };
    
    setFaultTreeModel(mergedModel);
    updateCountersFromModel(mergedModel); // Aggiorna i contatori
  }, [faultTreeModel, updateCountersFromModel]);

  // Gestione modifiche fault tree dal chatbot
  const handleModifyFaultTree = useCallback((modifications: FaultTreeModification[]) => {
    modifications.forEach(mod => {
      switch (mod.type) {
        case 'add':
          if (mod.elementType === 'event') {
            const newEvent: BaseEvent = {
              id: `event-${Date.now()}`,
              type: 'basic-event',
              name: mod.data.name || 'Nuovo Evento',
              position: mod.data.position || { x: 300, y: 200 },
              parameters: mod.data.parameters || {},
              failureProbabilityDistribution: mod.data.failureProbabilityDistribution || { type: 'exponential', lambda: 0.01 }
            };
            setFaultTreeModel(prev => ({
              ...prev,
              events: [...prev.events, newEvent]
            }));
          } else if (mod.elementType === 'gate') {
            const newGate: Gate = {
              id: `gate-${Date.now()}`,
              type: 'gate',
              gateType: mod.data.gateType || 'OR',
              name: mod.data.name || 'Nuova Porta',
              position: mod.data.position || { x: 300, y: 200 },
              inputs: [],
              isFailureGate: false,
              parameters: mod.data.parameters || {}
            };
            setFaultTreeModel(prev => ({
              ...prev,
              gates: [...prev.gates, newGate]
            }));
          }
          break;
          
        case 'remove':
          if (mod.elementId) {
            handleDeleteElement(mod.elementId);
          }
          break;
          
        case 'update':
          if (mod.elementId && mod.data) {
            setFaultTreeModel(prev => ({
              ...prev,
              events: prev.events.map(event => 
                event.id === mod.elementId ? { ...event, ...mod.data } : event
              ),
              gates: prev.gates.map(gate => 
                gate.id === mod.elementId ? { ...gate, ...mod.data } : gate
              )
            }));
          }
          break;
      }
    });
  }, [handleDeleteElement]);

  // Crea un nuovo modello vuoto
  const createNewModel = useCallback(() => {
    setFaultTreeModel({
      events: [],
      gates: [],
      connections: []
    });
    setSelectedElement(null);
    setShowParameterModal(false);
    setComponentToPlace(null);
    saveFaultTreeOpenedFile(null); // Reset del file aperto
    setNextEventNumber(1); // Reset contatore eventi
    setNextGateNumber(1); // Reset contatore porte
  }, [saveFaultTreeOpenedFile]);

  // Gestione nuovo modello
  const handleNewModel = useCallback(() => {
    // Controlla se ci sono modifiche non salvate
    if (faultTreeModel.events.length > 0 || faultTreeModel.gates.length > 0 || faultTreeModel.connections.length > 0) {
      setShowNewModelConfirmation(true);
    } else {
      // Se non ci sono modifiche, crea direttamente un nuovo modello
      createNewModel();
    }
  }, [faultTreeModel, createNewModel]);

  return (
    <div className={`fault-tree-editor ${isDarkMode ? 'dark-mode' : ''}`}>
      <MenuBar
          onSave={handleSaveFile}
          onOpenWithFileSystem={handleOpenFileWithFileSystem}
          onExportCode={handleExportCode}
          onShowSaveModal={handleShowSaveModal}
          onExportXML={handleExportXML}
          onExportCSV={handleExportCSV}
          onExportMatlab={handleShowMatlabExport}
          onShowLLMConfig={handleShowLLMConfig}
          onShowSHyFTA={handleShowSHyFTA}
          isDarkMode={isDarkMode}
          onToggleDarkMode={handleToggleDarkMode}
          onNewModel={handleNewModel}
          onClearAllModels={clearSnapshots}
          openedFile={openedFile}
          currentEditor="fault-tree"
          onNavigateToFaultTree={handleNavigateToFaultTree}
          onNavigateToMarkov={handleNavigateToMarkov}
          onLogout={handleLogout}
        />
      
      <div className={`editor-content ${isLeftPanelCollapsed ? 'left-panel-collapsed' : ''} ${isRightPanelCollapsed ? 'right-panel-collapsed' : ''}`}>
        <LeftPanel 
          onAddBaseEvent={handleAddBaseEvent}
          onAddGate={handleAddGate}
          clickToPlaceMode={clickToPlaceMode}
          onToggleClickToPlace={handleToggleClickToPlace}
          componentToPlace={componentToPlace}
          missionTime={missionTime}
          onMissionTimeChange={handleMissionTimeChange}
          isDarkMode={isDarkMode}
          isCollapsed={isLeftPanelCollapsed}
          onToggleCollapse={handleToggleLeftPanel}
        />
        
        <CentralPanel
          faultTreeModel={faultTreeModel}
          onElementClick={handleElementClick}
          onModelChange={handleModelChange}
          onDeleteElement={handleDeleteElement}
          onDeleteConnection={handleDeleteConnection}
          onPanelClick={handlePanelClick}
          componentToPlace={componentToPlace}
          isDarkMode={isDarkMode}
          disableDeletion={showParameterModal}
          onReorganizeComponents={handleReorganizeComponents}
          getCurrentPositionsRef={getCurrentPositionsRef}
        />
        
        <RightPanel 
          onGenerateFaultTree={handleGenerateFaultTree}
          onModifyFaultTree={handleModifyFaultTree}
          currentFaultTree={faultTreeModel}
          isDarkMode={isDarkMode}
          isCollapsed={isRightPanelCollapsed}
          onToggleCollapse={handleToggleRightPanel}
          editorType="fault-tree"
        />
      </div>

      {showParameterModal && selectedElement && (
        <ParameterModal
          key={`${selectedElement.id}-${simulationResultsVersion}`}
          element={selectedElement}
          onSave={handleUpdateElement}
          onClose={() => {
            setShowParameterModal(false);
            setSelectedElement(null);
          }}
          faultTreeModel={faultTreeModel}
          onShowResults={handleShowResults}
        />
      )}

      <SaveModal
        isOpen={showSaveModal}
        onClose={() => setShowSaveModal(false)}
        faultTreeModel={faultTreeModel}
      />

      <LLMConfigModal
        isOpen={showLLMConfigModal}
        onClose={() => setShowLLMConfigModal(false)}
        onConfigChange={handleLLMConfigChange}
      />

      <MatlabExportModal
        isOpen={showMatlabExportModal}
        onClose={() => setShowMatlabExportModal(false)}
        faultTreeModel={faultTreeModel}
        missionTime={missionTime}
      />

      <SHyFTAModal
        isOpen={showSHyFTAModal}
        onClose={() => setShowSHyFTAModal(false)}
        faultTreeModel={faultTreeModel}
        missionTime={missionTime}
        onShowCIResults={handleShowCIResults}
      />

      <ConfirmationModal
        isOpen={showNewModelConfirmation}
        onClose={() => setShowNewModelConfirmation(false)}
        onConfirm={createNewModel}
        title=" Nuovo Modello"
        message="Il modello esistente verr perduto se non  stato salvato. Sei sicuro di voler continuare?"
        confirmText="S, Nuovo Modello"
        cancelText="Annulla"
        confirmButtonClass="danger"
      />

      <ComponentResultsModal
        isOpen={showComponentResultsModal}
        onClose={() => {
          setShowComponentResultsModal(false);
          setSelectedResultsElementId(null);
        }}
        elementId={selectedResultsElementId}
      />

      <CIResultsModal
        isOpen={showCIResultsModal}
        onClose={() => setShowCIResultsModal(false)}
      />
    </div>
  );
};

// Funzione per generare il codice del fault tree
const generateFaultTreeCode = (model: FaultTreeModel): string => {
  let code = "// Dynamic Fault Tree Code\n\n";
  
  // Genera codice per eventi base
  code += "// EVENTI BASE\n";
  model.events.forEach(event => {
    code += `BaseEvent ${event.name} {\n`;
    code += `  id: "${event.id}"\n`;
    if (event.description) code += `  description: "${event.description}"\n`;
    if (event.failureRate) code += `  failureRate: ${event.failureRate}\n`;
    
    // Aggiungi distribuzione di probabilit di guasto
    if (event.failureProbabilityDistribution) {
      code += `  failureProbabilityDistribution: {\n`;
      code += `    type: "${event.failureProbabilityDistribution.type}"\n`;
      switch (event.failureProbabilityDistribution.type) {
        case 'exponential':
          code += `    lambda: ${event.failureProbabilityDistribution.lambda} // h\n`;
          break;
        case 'weibull':
          code += `    k: ${event.failureProbabilityDistribution.k} // adimensionale\n`;
          code += `    lambda: ${event.failureProbabilityDistribution.lambda} // h\n`;
          code += `    mu: ${event.failureProbabilityDistribution.mu} // h\n`;
          break;
        case 'normal':
          code += `    mu: ${event.failureProbabilityDistribution.mu} // h\n`;
          code += `    sigma: ${event.failureProbabilityDistribution.sigma} // h\n`;
          break;
        case 'constant':
          code += `    probability: ${event.failureProbabilityDistribution.probability} // adimensionale\n`;
          break;
      }
      code += `  }\n`;
    }

    // Aggiungi distribuzione di probabilit di riparazione
    if (event.repairProbabilityDistribution) {
      code += `  repairProbabilityDistribution: {\n`;
      code += `    type: "${event.repairProbabilityDistribution.type}"\n`;
      switch (event.repairProbabilityDistribution.type) {
        case 'exponential':
          code += `    lambda: ${event.repairProbabilityDistribution.lambda} // h\n`;
          break;
        case 'weibull':
          code += `    k: ${event.repairProbabilityDistribution.k} // adimensionale\n`;
          code += `    lambda: ${event.repairProbabilityDistribution.lambda} // h\n`;
          code += `    mu: ${event.repairProbabilityDistribution.mu} // h\n`;
          break;
        case 'normal':
          code += `    mu: ${event.repairProbabilityDistribution.mu} // h\n`;
          code += `    sigma: ${event.repairProbabilityDistribution.sigma} // h\n`;
          break;
        case 'constant':
          code += `    probability: ${event.repairProbabilityDistribution.probability} // adimensionale\n`;
          break;
      }
      code += `  }\n`;
    }
    
    if (event.parameters) {
      Object.entries(event.parameters).forEach(([key, value]) => {
        code += `  ${key}: ${JSON.stringify(value)}\n`;
      });
    }
    code += "}\n\n";
  });

  // Genera codice per porte
  code += "// PORTE\n";
  model.gates.forEach(gate => {
    code += `Gate ${gate.name} {\n`;
    code += `  id: "${gate.id}"\n`;
    code += `  type: "${gate.gateType}"\n`;
    if (gate.description) code += `  description: "${gate.description}"\n`;
    if (gate.inputs.length > 0) {
      code += `  inputs: [${gate.inputs.map(id => `"${id}"`).join(', ')}]\n`;
    }
    if (gate.parameters) {
      Object.entries(gate.parameters).forEach(([key, value]) => {
        code += `  ${key}: ${JSON.stringify(value)}\n`;
      });
    }
    code += "}\n\n";
  });

  // Genera codice per connessioni
  if (model.connections.length > 0) {
    code += "// CONNESSIONI\n";
    model.connections.forEach(conn => {
      code += `Connection: ${conn.source} -> ${conn.target}\n`;
    });
  }

  return code;
};

export default FaultTreeEditor;
